import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { MobileToolbarItem } from '../types';

interface PanelProps {
  controller?: Controller;
  openPanel?: (item: MobileToolbarItem) => void;
  closePanel?: (autoFocus?: boolean) => void;
  item?: MobileToolbarItem;
}

interface SubLineProps {
  item?: MobileToolbarItem;
  openSubLine?: (item: MobileToolbarItem) => void;
  closeSubLine?: () => void;
}

type ItemClickProps = (item: MobileToolbarItem) => void;

// 底部弹出面板
export const PanelContext = React.createContext<PanelProps>({});
// 浮出二级菜单（文字业务暂未使用）
export const SublineContext = React.createContext<SubLineProps>({});
// 全局点击事件
export const ItemClickContext =
  React.createContext<ItemClickProps | undefined>(undefined);

export function usePanel() {
  return React.useContext(PanelContext);
}

export function useSubline() {
  return React.useContext(SublineContext);
}

export function useItemClick() {
  return React.useContext(ItemClickContext);
}
